<template>
<section>
  <transition name="home">
    <div id="home" v-show="!$router.currentRoute.name">
      <i-header :vert="vert" :title="'众 安'"></i-header>
      <content v-on-scroll="onScroll">
        <div class="scroll-content">
          <transition name="swiper">
              <swiper :items="banner"  v-show="productions.length"></swiper>
          </transition>
          <transition name="shortcuts">
              <shortcuts v-if="productions.length"></shortcuts>
          </transition>
          <transition name="list">
              <div  v-if="productions.length">
                  <list :items="productions"></list>
                  <i-footer></i-footer>
              </div>
          </transition>
        </div>
      </content>
    </div>
  </transition>
  <router-view></router-view>
  </section>
</template>

<script>
import iHeader from '../components/header'
import swiper from '../components/swiper'
import shortcuts from '../components/shortcuts'
import list from '../components/list'
import iFooter from '../components/footer'
export default {
  name: 'home',
  data () {
    return {
      banner: [],
      productions:[],
      vert:false,
      contentElement:null,
    }
  },
  components: {
    iHeader,
    swiper,
    shortcuts,
    list,
    iFooter
  },
  methods: {
    onbeforeunload () {
      return window.onbeforeunload=()=>{
        this.$loading.show("正在前往...",3000);
      }
    },
    destroyEvents (){
      window.onbeforeunload=null;
      this.content=null
    },
    onScroll (event) {
       let top=event.target.scrollTop;
        //console.log(top);
        if(top>160) this.vert=true
        else if(top<200) this.vert=false
    }
  },
  created () {
    this.$http.getBanners().then(res=>{
      this.banner=res;
    })
    this.$http.getProductions().then(res=>{
      this.productions=res;
      this.$loading.hide();
    })
  },
  mounted (){

    this.$loading.show();

    this.onbeforeunload();
  },
  beforeDestroy () {
    this.destroyEvents()
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    @import '../../static/scss/mixin.scss';
    @mixin enter-active($property,$duration){
        @include transform($property);
        @include transition(all $duration cubic-bezier(0.4,0,0.6,1));
        will-change:transform,-webkit-transform;
    }

    $transition-duration:480ms;
    .swiper-enter-active {
        @include enter-active(translateY(0),$transition-duration);
    }

    .swiper-enter {
        @include transform(translateY(-300px))
    }
    .swiper-leave-active {
        @include enter-active(translateY(300px),$transition-duration);
    }
    .swiper-leave {
        @include transform(translateY(0))
    }


    .shortcuts-enter-active {
        @include enter-active(translateX(0),$transition-duration);
    }
    .shortcuts-enter {
         @include transform(translateX(100%))
    }
    .shortcuts-leave-active {
        @include enter-active(translateX(-100%),$transition-duration);
    }


    .list-enter-active {
        @include enter-active(translateX(0),$transition-duration);

    }
    .list-enter {
        @include transform(translateX(-100%))
    }
    .list-leave-active {
        @include enter-active(translateX(100%),$transition-duration);
    }

    .list-leave,.shortcuts-leave {
        @include transform(translateX(0))
    }

</style>



