
import Vue from 'vue'
let scrollHandler=null
Vue.directive('onScroll', {

    bind: function(el, binding) {　　　
		if (el.childElementCount!==1) return;
		scrollHandler=event=>binding.value(event);
		setTimeout(()=>
	        el.firstChild.onscroll=scrollHandler,500);
    },
    unbind (el) {
	   el.firstChild.removeEventListener('scroll', scrollHandler);
	}

});



