<template>
    <transition name="loading">
        <div v-if="show" class="i-loading">
          <div class="toast">
            <i class="loading icon-toast"></i>
            <p class="toast-content">{{text}}</p>
          </div>
        </div>
    </transition>
</template>
<script>

  export default{
    data () {
        return {
            show: false,
            text: "加载中...",
            timer:null
        }
    },
    created () {
        this.$loading.on('loadingshow',event=>{
            clearTimeout(this.timer);
            event=event||{};
            this.text=event.text||"加载中...";
            this.show=true;
            this.timer=event.time?setTimeout(()=>this.show=false,event.time):null;
        });
        this.$loading.on('loadinghide',event=>this.show=false);
    }
  }
</script>
<style lang="scss" scoped>
    @import '../../static/scss/mixin.scss';
    .loadingr-enter-active {
         @include transition(all .5s cubic-bezier(0.4,0,0.6,1));
        @include transform(translateY(0));
        opacity:1;
        will-change:-webkit-transform,opacity;
    }

    .loading-enter {
        opacity:0;
        @include transform(translateY(-50vh));
    }
    .loading-leave-active {
        // transition:all .5s;
        // -webkit-transition:all .5s;
        @include transition(all .5s cubic-bezier(0.4,0,0.6,1));
        @include transform(translateY(50vh));
        // transform:translateY(50vh);
        // -webkit-transform:translateY(50vh);
        opacity:0;
        will-change:-webkit-transform,opacity,transform;
    }
    .loading-leave {
        transform:translateY(0);
        -webkit-transform:translateY(0);
        opacity:1;
    }

    .i-loading{
        position: absolute;
        z-index: 5000;
        top:0;
        bottom:0;
        left:0;
        right:0;
        background: hsla(0,0%,7%,.7);
        height:100px;
        width:100px;
        text-align: center;
        border-radius: 6px;
        color: #fff;
        margin:auto;
    }
    .icon-toast.loading {
        margin: 20px 0 0;
        width: 38px;
        height: 38px;
        vertical-align: super;
    }
    .loading {
        width: 20px;
        height: 20px;
        display: inline-block;
        vertical-align: middle;
        -webkit-animation: rot 1s steps(12) infinite;
        animation: rot 1s steps(12) infinite;
        background: transparent url(data:image/png;base64,) no-repeat;
        background-size: 100%;
    }
    .toast-content {
        font-size:1.4rem;
        font-weight: 600;
    }
    @-webkit-keyframes rot {
        0% {

            @include transform(rotate(0deg));
        }

        to {

            @include transform(rotate(1turn));
        }
    }

    @keyframes rot {
        0% {
            @include transform(rotate(0deg));
        }

        to {

            @include transform(rotate(1turn));
        }
    }


</style>


